import * as React from "react";
import {
  Keyboard,
  StyleSheet,
  View,
  Platform,
  Dimensions,
  Button
} from "react-native";
import AppBar from "./AppBar";
// import { Provider as PaperProvider } from "react-native-paper";
import Editor from "./Editor";
import Preview from "./Preview";
import ErrorBoundary from "react-native-error-boundary";

import { Container, Text, Grid, Col, Row, StyleProvider, Drawer } from "native-base";
import * as Font from "expo-font";
import { Ionicons } from "@expo/vector-icons";
import getTheme from "./native-base-theme/components";
import material from "./native-base-theme/variables/material";

const { useState, useEffect } = React;

export default function App() {
  const CustomFallback = (props: { error: Error; resetError: Function }) => (
    <View
      style={{
        flex: 1,
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center"
      }}
    >
      <Text>Something happened!</Text>
      <Text>{props.error.toString()}</Text>
      <Button onPress={() => props.resetError()} title={"Try again"} />
    </View>
  );

  const [value, setValue] = useState(
    "# Why is markdown cool?\n\n" +
      "* because it lets us do simple formatting **easily** \n" +
      "* _without_ the need for complex CMS data structures \n" +
      "* and you can outsource ~~your~~ work to the content creators! \n\n" +
      "> This is a blockquote \n\n" +
      "![We can add images!](http://lancemock.com/dream_court_400.jpg) \n" +
      "[Or link to places](http://lancemock.com) \n\n" +
      "# Why is markdown cool?\n\n" +
      "* because it lets us do simple formatting **easily** \n" +
      "* _without_ the need for complex CMS data structures \n" +
      "* and you can outsource ~~your~~ work to the content creators! \n\n" +
      "> This is a blockquote \n\n" +
      "![We can add images!](http://lancemock.com/dream_court_400.jpg) \n" +
      "[Or link to places](http://lancemock.com) \n"
  );

  const PORTRAIT = "P";
  const LANDSCAPE = "L";
  const WEB_OS = "web";

  const [orientation, setOrientation] = useState(PORTRAIT);

  const h = (Dimensions.get("window").height - 50) / 2;

  const onChange = ({ window }) => {
    if (window.height < window.width) setOrientation(LANDSCAPE);
    else setOrientation(PORTRAIT);
  };

  useEffect(() => {
    Font.loadAsync({
      Roboto: require("native-base/Fonts/Roboto.ttf"),
      Roboto_medium: require("native-base/Fonts/Roboto_medium.ttf"),
      ...Ionicons.font
    });

    //@ts-ignore
    Dimensions.addEventListener("change", onChange);
    return () => {
      //@ts-ignore
      Dimensions.removeEventListener("change", onChange);
    };
  }, []);

  const [kbIsVisible, setKBIsVisible] = useState(false);
  useEffect(() => {
    Keyboard.addListener("keyboardDidShow", () => setKBIsVisible(true));
    Keyboard.addListener("keyboardDidHide", () => setKBIsVisible(false));
    return () => {
      Keyboard.removeAllListeners("keyboardDidShow");
      Keyboard.removeAllListeners("keyboardDidHide");
    };
  }, []);

  const [open, setOpen] = useState(false);
  const drawerStyles = {
    drawer: { shadowColor: '#000000', shadowOpacity: 0.8, shadowRadius: 3},
    main: {paddingLeft: 3},
  }
  return (
    <ErrorBoundary FallbackComponent={CustomFallback}>
      <StyleProvider style={getTheme(material)}>
        <Container>
          <Drawer
            open={open}
            type="overlay"
            content={<View style={{backgroundColor: "white"}}><Text>hello</Text></View>}
            tapToClose={true}
            openDrawerOffset={0.8}
            panCloseMask={0.2}
            closedDrawerOffset={-3}
            styles={drawerStyles}
            tweenHandler={(ratio) => ({
              main: { opacity:(2-ratio)/2 }
            })}
            onClose={()=>setOpen(false)}
          >
          <AppBar />
          <Grid>
            {(Platform.OS === WEB_OS || orientation === LANDSCAPE) && (
              <Row>
                <Col style={styles.colRow}>
                  <Editor
                    markdownContent={value}
                    setMarkdownContent={setValue}
                  />
                </Col>
                <Col style={styles.colRow}>
                  <Button onPress={() => setOpen(true)} label="open"/>
                  <Preview markdownContent={value} />
                </Col>
              </Row>
            )}
            {Platform.OS !== WEB_OS && orientation === PORTRAIT && (
              <Col>
                <Row style={styles.colRow}>
                  <Editor
                    markdownContent={value}
                    setMarkdownContent={setValue}
                  />
                </Row>
                {!kbIsVisible && (
                  <Row style={styles.preview}>
                    <Preview markdownContent={value} />
                  </Row>
                )}
              </Col>
            )}
          </Grid>
          </Drawer>

        </Container>
      </StyleProvider>
    </ErrorBoundary>
  );
}

const styles = StyleSheet.create({
  colRow: {
    borderWidth: 1,
    borderRadius: 5,
    borderColor: "black",
    margin: 10,
    backgroundColor: "#ddd"
  },
  preview: {
    borderWidth: 1,
    borderRadius: 5,
    borderColor: "black",
    margin: 10,
    padding: 10,
    backgroundColor: "#ddd"
  }
});
