import * as React from 'react'
import { Platform, StyleSheet, TextInput } from 'react-native';

interface Props {
  markdownContent: string;
  setMarkdownContent: (value: string) => void;
}

const Editor = (props: Props) => {
    return (
      <TextInput
        multiline={true}
        style={[styles.textinput, Platform.OS === "web" &&  {height: "100%"}]}
        value={props.markdownContent}
        onChangeText={(text: string) => props.setMarkdownContent(text)}
      />
    )
}

export default Editor;

const styles = StyleSheet.create({
  textinput: {
    flex: 1,
    alignItems: "stretch",
    padding: 20
  }
});
